ackground: -webkit-gradient(linear, left top, right bottom, from(#FFFFFF),  to(#D8D4DC));\n  background: linear-gradient(to bottom right, #FFFFFF 0%,  #D8D4DC 100%);\n  min-height: 100vh;  \n  display: inline-block;\n}\n#orders {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 65%;\n  font-family: 'Lato', 'sans-serif';\n}\n#orders-main {\n  border-bottom: 2px solid #8e8b91;\n}\n#orders h3 {\n  letter-spacing: 1px;\n  text-transform: uppercase;\n  font: 25px 'Roboto';\n  color: #29105b;\n  text-align:left;\n}\n#company-name, h4 {\n  letter-spacing: 1px;\n  text-transform: uppercase;\n  font: 18px 'Roboto';\n  color: #6f6d72;\n  font-style: 'bold';\n  text-align:left;\n}\n#orders-main, h5 {\n  letter-spacing: 1px;\n  font: 25px 'Roboto';\n  color: #29105b;\n  text-align:left;\n}\n#order-history h6 {\n  letter-spacing: 1px;\n  font: 20px 'Roboto';\n  color: #29105b;\n  text-align:left;\n  text-transform: uppercase;\n  /* text-shadow: 2px 2px 4px #D8D4DC;  */\n}\n#order-history p {\n  font: 16px 'Roboto';\n  color: #29105b;\n}\n/* timeline */\n#payment-history {\n  padding-left: 20px;\n  padding-top: 20px;\n}\nul.timeline {\n  list-style-type: none;\n  position: relative;\n}\nul.timeline:before {\n  content: ' ';\n  background: #31037a;\n  display: inline-block;\n  position: absolute;\n  left: 29px;\n  width: 2px;\n  height: 100%;\n  z-index: 400;\n}\nul.timeline > li {\n  margin: 20px 0;\n  padding-left: 20px;\n}\nul.timeline > li:before {\n  content: ' ';\n  background: white;\n  display: inline-block;\n  position: absolute;\n  border-radius: 50%;\n  border: 3px solid #31037a;\n  left: 20px;\n  width: 20px;\n  height: 20px;\n  z-index: 400;\n}\n/* orders Responsive ish */\n.responsive-component.small body.orders #orders{\n  padding-left: 10px;\n  padding-right: 10px; \n  padding-bottom: 100px;  \n  width: 100%;\n}\n.responsive-component.small #orders {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 100%;\n}\n/* ----