\n.container #purchase_content {\n  /* border-top: 2px solid #7e6f89; */\n  border-top: 2px solid purple;\n  padding-top: 20px;\n}\n.tabs {\n  position: relative;\n  margin: 0 auto;\n}\n.tabs__item {\n  display: inline-block;\n  margin: 0 5px;\n  padding: 10px;\n  padding-bottom: 8px;\n  font-size: 18px;\n  letter-spacing: 0.8;\n  color: rgb(94, 94, 94);\n  text-decoration: none;\n  border: none;\n  background-color: transparent;\n  /* border-bottom: 2px solid white; */\n  cursor: pointer;\n  -webkit-transition: all 0.25s;\n  transition: all 0.25s;\n}\n.tabs__item_active {\n  color: #29105b;\n}\n.tabs__item:hover {\n  border-bottom: 2px solid #ffffff;\n  color: #29105b;\n}\n.tabs__item:focus {\n  outline: none;\n  border-bottom: 3px solid #ffffff;\n  color: #29105b;\n}\n.tabs__item:first-child {\n  margin-left: 0;\n}\n.tabs__item:last-child {\n  margin-right: 0;\n}\n.tabs__active-line {\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  height: 2px;\n  /* background-color: black; */\n  -webkit-transition: width 0.4s ease, -webkit-transform 0.4s ease;\n  transition: width 0.4s ease, -webkit-transform 0.4s ease;\n  transition: transform 0.4s ease, width 0.4s ease;\n  transition: transform 0.4s ease, width 0.4s ease, -webkit-transform 0.4s ease;\n}\n", "", {"version":3,"sources":["/Users/tbui/Projects/morning_waters/src/assets/css/settings.css","/Users/tbui/Projects/morning_waters/src/components/src/components/SettingsPage.vue"],"names":[],"mappings":";AAAA;EACE,sBAAsB;EACtB,kBAAkB;CACnB;AAED;EACE,eAAe;EACf,iBAAiB;CAClB;AAED;;;EAGE,qBAAqB;CACtB;AAED;;;EAGE,cAAc;CACf;AAED,gDAAgD;AAChD;EACE,kBAAkB;EAClB,kBAAkB;EAClB,mBAAmB;EACnB,sBAAsB;EACtB,gBAAgB;EAChB,0FAAwE;EAAxE,wEAAwE;EACxE,uBAAuB;EACvB,kBAAkB;CACnB;AAED;EACE,iCAAiC;CAClC;AAED;EACE,eAAe;EACf,kBAAkB;EAClB,mBAAmB;EACnB,WAAW;CACZ;AAED;EACE,cAAc;EACd,WAAW;EACX,kBAAkB;CACnB;AAED;EACE,oBAAoB;EACpB,oBAAoB;EACpB,uBAAuB;EACvB,gBAAgB;CACjB;AAED;EACE,oBAAoB;EACpB,0BAA0B;EAC1B,oBAAoB;EACpB,eAAe;EACf,gBAAgB;EAChB,uCAAuC;CACxC;