top: 80px;\n  padding-left: 0px;\n  padding-right: 0px; \n  padding-bottom: 100px;   \n  text-align:left;\n  background: linear-gradient(to bottom right, #FFFFFF 0%,  #D8D4DC 100%);\n  background-size: cover;\n  min-height: 100vh;  \n}\n\n#settings-nav {\n  border-bottom: 2px solid #29105b;\n}\n\n#settings-page {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 50%;\n}\n\n.settings-content{\n  display:block;\n  width:100%;\n  padding-left:20px;\n}\n\n#settings-page, label {\n  letter-spacing: 1px;\n  font: 15px 'Roboto';\n  color: rgb(94, 94, 94);\n  text-align:left;\n}\n\n#settings-page h3 {\n  letter-spacing: 1px;\n  text-transform: uppercase;\n  font: 30px 'Roboto';\n  color: #29105b;\n  text-align:left;\n  /* text-shadow: 2px 2px 4px #D8D4DC; */\n}\n\n#settings-page h4 {\n  letter-spacing: 1px;\n  text-transform: uppercase;\n  font: 18px 'Roboto';\n  font-style: medium;\n  color: #29105b;\n  text-align:left;\n  /* text-shadow: 2px 2px 4px #D8D4DC; */\n}\n\n#settings-page p {\n  font: 17px 'Roboto';\n  color: rgb(94, 94, 94);\n  text-align:left;\n}\n\n/* Responsive ish */\n.responsive-component.small body.settings{\n  padding-left: 10px;\n  padding-right: 10px;\n}\n\n.responsive-component.small #settings-page {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 100%;  \n}\n\n\n/* -----------------orders---------------- */\nbody.orders {\n  padding-top: 100px;\n  padding-left: 100px;\n  padding-right: 100px; \n  padding-bottom: 100px;  \n  text-align:left;\n  background: linear-gradient(to bottom right, #FFFFFF 0%,  #D8D4DC 100%);\n  min-height: 100vh;  \n  display: inline-block;\n}\n\n#orders {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 65%;\n  font-family: 'Lato', 'sans-serif';\n}\n\n#orders-main {\n  border-bottom: 2px solid #8e8b91;\n}\n\n#orders h3 {\n  letter-spacing: 1px;\n  text-transform: uppercase;\n  font: 25px 'Roboto';\n  color: #29105b;\n  text-align:left;\n}\n\n#company-name, h4 {\n  letter-spacing: 1px;\n  text-transform: uppercase;\n  font: 18px 'Roboto';\n  color: #6f6d72;\n  font-style: 'bold';\n  text-align:left;\n}\n\n#orders-main, h5 {\n  letter-spacing: 1px;\n  font: 25px 'Roboto';\n  color: #29105b;\n  text-align:left;\n}\n\n#order-history h6 {\n  letter-spacing: 1px;\n  font: 20px 'Roboto';\n  color: #29105b;\n  text-align:left;\n  text-transform: uppercase;\n  /* text-shadow: 2px 2px 4px #D8D4DC;  */\n}\n\n#order-history p {\n  font: 16px 'Roboto';\n  color: #29105b;\n}\n\n/* timeline */\n#payment-history {\n  padding-left: 20px;\n  padding-top: 20px;\n}\n\nul.timeline {\n  list-style-type: none;\n  position: relative;\n}\n\nul.timeline:before {\n  content: ' ';\n  background: #31037a;\n  display: inline-block;\n  position: absolute;\n  left: 29px;\n  width: 2px;\n  height: 100%;\n  z-index: 400;\n}\nul.timeline > li {\n  margin: 20px 0;\n  padding-left: 20px;\n}\nul.timeline > li:before {\n  content: ' ';\n  background: white;\n  display: inline-block;\n  position: absolute;\n  border-radius: 50%;\n  border: 3px solid #31037a;\n  left: 20px;\n  width: 20px;\n  height: 20px;\n  z-index: 400;\n}\n\n/* orders Responsive ish */\n.responsive-component.small body.orders #orders{\n  padding-left: 10px;\n  padding-right: 10px; \n  padding-bottom: 100px;  \n  width: 100%;\n}\n\n.responsive-component.small #orders {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 100%;  \n}\n\n/* -----------------Credits Purchasing----------------- */\nbody.credits-purchasing {\n  padding-top: 100px;\n  padding-left: 100px;\n  padding-right: 100px; \n  padding-bottom: 100px;   \n  text-align:left;\n  background: linear-gradient(to bottom right, #FFFFFF 0%,  #D8D4DC 100%); \n  background-size: cover;\n  min-height: 100vh;  \n}\n\n.container #credits{\n  width: 65%\n}\n\n#credits h4 {\n  letter-spacing: 1px;\n  text-transform: uppercase;\n  font: 20px 'Roboto';\n  color: #7e6f89;\n  font-style: 'medium';\n  /* color: #31037a; */\n  text-align:left;\n}\n\n#credits h5 {\n  letter-spacing: 1px;\n  font: 33px 'Roboto';\n  color: #7e6f89;\n  text-align:left;\n}\n\n.responsive-component.small body.credits-purchasing{\n  padding-left: 10px;\n  padding-right: 10px;\n}\n\n.responsive-component.small .container #orders {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 100%;  \n}\n\n/* -----------------Shopping Cart---------------- */\nbody.shopping-cart {\n  background-size: cover;\n  padding-bottom:10px;\n}\n\nbody.shopping-cart .table {\n  background: white;\n  border-color: purple;\n  border-width: 3px;\n}\n\n/* -----------------BuyersCarts---------------- */\n\nbody.buyers_carts{\n  padding-top: 90px;\n  background-size: cover;\n  background: linear-gradient(to bottom right, #FFFFFF 0%,  #D8D4DC 100%); \n  min-height: 100vh;  \n}\n/* -----------------Checkout---------------- */\n\nbody.buyer-checkout {\n  padding-top: 90px;\n  background-size: cover;\n  background: linear-gradient(to bottom right, #FFFFFF 0%,  #D8D4DC 100%);\n  min-height: 100vh;  \n}\n\n.buyer-checkout h1 {\n  color: #361555;\n  letter-spacing: 1px;\n  text-transform: uppercase;\n  font: 20px 'Roboto';\n  font-style: 'medium';\n}\n\n.container #purchase_content {\n  /* border-top: 2px solid #7e6f89; */\n  border-top: 2px solid purple;\n  padding-top: 20px;\n}\n","<template>\n<div id=\"app\">\n  <buyer-header></buyer-header>\n  <div class=\"responsive-component\" v-responsive=\"{\n      small: el => el.width < 860\n      }\">\n    <body class=\"buyer-checkout\">\n      <div\n        class=\"container\"\n        style=\"max-width:800px; display: block;\n          margin-left: auto;\n          margin-right: auto;\"\n      >\n        <h5 style=\"text-align:center;\">Request Checkout</h5>\n        <br />\n\n        <md-card md-with-hover style=\"background-color: white;\">\n          <div>\n            <md-card-content>\n              <div v-if=\"this.customerDefaultSource !== null\" class=\"default-source\">\n                <div class=\"change-preference\">\n                  <router-link\n                    class=\"nav-link\"\n                    to=\"/dashboard/changeDefaultSource\"\n                  >Change Preference</router-link>\n                </div>\n                <br />\n                <div class=\"default-payment-card\">\n                  Default Payment Method:\n                  <pre>{{ this.customerDefaultSource.brand }} ending in\t{{ this.customerDefaultSource.last4 }} </pre>\n                  <pre> expires : {{ this.customerDefaultSource.exp_month }} / {{ this.customerDefaultSource.exp_year }}</pre>\n                </div>\n              </div>\n\n              <div v-else>No default payment method</div>\n\n              <br />\n              <br />\n              <br />\n              <!-- make input for card -->\n            </md-card-content>\n            <div id=\"stripeCard\">\n              <div ref=\"card\" style=\"border-bottom: 1px inset black\"></div>\n            </div>\n            <br />\n            <br />\n          </div>\n        </md-card>\n        <br />\n        <br />\n        <h2\n          style=\"text-align:center;color:purple;\"\n        >You will not be charged until the seller confirms your order.</h2>\n\n        <h3>\n          <md-button\n            class=\"checkout-btn md-raised md-accent\"\n            style=\"background-color: white; color: #311c63;\"\n            v-if=\"this.shoppingCart.length > 0 && this.quoteRequestsCart.length\"\n            type=\"submit\"\n            @click=\"sendOrderAndOrInquiries()\"\n          >Send Order</md-button>\n        </h3>\n        <br />\n\n        <md-button\n          class=\"checkout-btn md-raised md-accent\"\n          style=\"background-color: white; color: #311c63;\"\n          v-if=\"this.shoppingCart.length === 0 && this.quoteRequestsCart.length > 0\"\n          @click=\"sendQuoteRequests()\"\n        >Send Inquires</md-button>\n\n        <div class=\"container\" id=\"purchase_content\">\n          <shopping-cart v-if=\"this.shoppingCart.length > 0\"></shopping-cart>\n\n          <br />\n          <request-quote-cart v-if=\"this.quoteRequestsCart.length > 0\"></request-quote-cart>\n        </div>\n\n        <button\n          v-if=\"this.shoppingCart.length > 0 && this.quoteRequestsCart.length === 0\"\n          type=\"submit\"\n          class=\"btn-cart btn-default btn-block\"\n          @click=\"sendShoppingCart()\"\n        >\n          <h3>Complete Purchase</h3>\n        </button>\n\n        <br />\n        <br />\n      </div>\n    </body>\n  </div>\n</div>\n</template>\n\n<script src=\"./scripts/buyerCheckout.js\">\n</script>\n\n<style scoped>\n@import \"../../assets/css/settings.css\";\n@import url(\"https://fonts.googleapis.com/css?family=Lato|Roboto\");\n\n#stripeCard {\n  margin-left: auto;\n  margin-right: auto;\n  margin-top: auto;\n  width: 50%;\n}\n.change-preference {\n  position: absolute;\n  left: 210px;\n  font-size: 80%;\n}\n.default-payment-card {\n  width: 300px;\n  border: 2px solid purple;\n  padding: 20px;\n  margin-left: 10px;\n}\n\n.add-payment-button {\n  position: relative;\n  margin: 25px;\n  bottom: 0;\n  right: 0;\n}\n\n.grid-container {\n  display: grid;\n  grid-template-columns: auto auto auto;\n  background-color: #2196f3;\n  padding: 10px;\n}\n.grid-item {\n  background-color: rgba(255, 255, 255, 0.8);\n  border: 1px solid rgba(0, 0, 0, 0.8);\n  padding: 2px;\n  font-size: 15px;\n  text-align: center;\n}\n</style>\n"],"sourceRoot":""}]);

