nt: 28px 'Roboto';\n    color: #29105b;\n    text-align:center;\n}\n\n#form-footer{\n    padding-bottom:5px;\n    padding-top:15px;\n    text-align: center;\n}\n#form-footer p {\n    text-align: center;\n    font: 12px 'Roboto';\n    color: rgb(129, 114, 114);\n}\n\n\n/* Card Formatting */\n\n.card {\n    margin-bottom: 0px;\n    max-width:500px;\n    margin-left: auto;\n    margin-right:auto;\n    border-radius: 10px;\n}\n\n/* .card #rounded {\n    border-radius: 10px;\n} */\n\n\n/* Container Formatting */\n\n.container#center {\n    /* margin-left: 0 auto;  */\n    /* shorthand for:\n    margin-top: 0;\n    margin-bottom: 0;\n    margin-left: auto;\n    margin-right: auto; */\n    text-align:center;\n}\n\n.container p {\n    text-align: center;\n    font: 12px 'Roboto';\n    color: rgb(129, 114, 114);\n}\n\n/* --------- Register Form---------- */\n\n\n\nform {\n    border-radius: 5px;\n    max-width:600px;\n    width:100%;\n    /* margin: 5% auto; */\n    padding: 5px 15px;\n    background-color: #FFFFFF;\n    overflow: hidden; \n    border-radius: 25px;\n}\n\n.center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 100%;\n    height:auto;\n    animation-name: moveInLeft;\n    animation-duration: 2s;\n}\n\n#register{\n    border-radius: 10px;\n}\n\n#register, label{\n    font: 18px 'Roboto';\n    color: rgb(94, 94, 94);\n    text-align:left;\n}\n\n#personalinfo {\n    width: 49.5%; \n    float:left;\n    border-right: 1px dotted #CCC;\n    box-sizing: border-box;\n    padding: 0px 15px 0px 0px;\n    overflow: hidden;\n    clear: both;\n}\n  \n#companyinfo {\n    width:49.5%;\n    float:right;\n    box-sizing: border-box;\n    padding: 0px 0px 0px 15px;\n    overflow: hidden;\n    clear: both;\n}\n\n#register-form-footer{\n    border-top: 2px #917e9e;\n}\n\n\n.fixed-horizontal-center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 50%;  \n}\n\n.error {\n    color: red;\n}\n\n\np {\n    text-align: left;\n    color: white;\n    /* font-size: 15px */\n    font: 20px \"roboto\";\n}\n\n#btn-login {\n    color: white;\n    border-color: #361555;\n    background-color: #361555;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-submit {\n    color: white;\n    border-color: #361555;\n    background-color: #361555;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-submit:hover {\n    color: white;\n    border-color: #850696;\n    background-color: #850696;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-next {\n    color: white;\n    border-color: #850696;\n    background-color: #850696;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n#btn-next:hover {\n    color: white;\n    border-color: #b109c7;\n    background-color: #b109c7;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-previous {\n    color: #850696;\n    border-color: #850696;\n    background-color: white;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-previous:hover {\n    color: #a109c7;\n    border-color: #a109c7;\n    background-color: rgb(228, 228, 228);\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n\n\n/* Responsive ish */\n\n.responsive-component.small .fixed-horizontal-center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 90%;  \n}\n\n\n/* ------Login Form--------- */\n\n#login {\n    /* margin-bottom:10px;\n    top: 25px;\n    left: 50%;\n    width: 350px;\n    -webkit-transform: translateX(-50%);\n    -moz-transform: translateX(-50%);\n    -ms-transform: translateX(-50%);\n    -o-transform: translateX(-50%);\n    transform: translateX(-50%); */\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 350px;  \n}\n\n#login_form-footer{\n    padding-bottom:5px;\n    padding-top:15px;\n    text-align: center;\n    width: 350px;\n}\n\n#login h1 {\n    text-align: center;\n    text-transform: uppercase;\n}\n\n\n\n.login #login label{\n    font: 18px 'Roboto';\n    color: rgb(94, 94, 94);\n    text-align:left;\n}\n\n.login #login h1 {\n    font: 28px 'Roboto';\n    color: #29105b;\n}\np {\n    font-size: 10px;\n    text-align: left;\n}\n\n\n\n\n#legal-links {\n    letter-spacing: 1px;\n    font: 15px 'Roboto';\n    text-align: center;\n    /* color: #917e9e; */\n    color: #29105b;\n}\n\n\n\n#login-logo {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 175px;\n    height:150px;\n}\n\n/* loading animation - replace filled form with loading animation*/\n.loader {\n    color: #3c1760;\n    font-size: 20px;\n    margin: 100px auto;\n    width: 1em;\n    height: 1em;\n    border-radius: 50%;\n    position: relative;\n    text-indent: -9999em;\n    -webkit-animation: load4 1.3s infinite linear;\n    animation: load4 1.3s infinite linear;\n    -webkit-transform: translateZ(0);\n    -ms-transform: translateZ(0);\n    transform: translateZ(0);\n}\n\n@-webkit-keyframes load4 {\n0%,\n100% {\n    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;\n}\n12.5% {\n    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;\n}\n25% {\n    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;\n}\n37.5% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;\n}\n50% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;\n}\n62.5% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em