;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAED,mEAAmE;AACnE;IACI,eAAe;IACf,gBAAgB;IAChB,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,mBAAmB;IACnB,qBAAqB;IACrB,8DAA8C;IAC9C,sDAAsC;IACtC,iCAAiC;IAEjC,yBAAyB;CAC5B;AAED;AACA;;IAEI,8IAAsI;YAAtI,sIAAsI;CACzI;AACD;IACI,4IAAoI;YAApI,oIAAoI;CACvI;AACD;IACI,8IAAsI;YAAtI,sIAAsI;CACzI;AACD;IACI,kJAA0I;YAA1I,0IAA0I;CAC7I;AACD;IACI,gJAAwI;YAAxI,wIAAwI;CAC3I;AACD;IACI,4IAAoI;YAApI,oIAAoI;CACvI;AACD;IACI,kJAA0I;YAA1I,0IAA0I;CAC7I;AACD;IACI,6IAAqI;YAArI,qIAAqI;CACxI;CACA;AACD;AACA;;IAEI,8IAAsI;YAAtI,sIAAsI;CACzI;AACD;IACI,4IAAoI;YAApI,oIAAoI;CACvI;AACD;IACI,8IAAsI;YAAtI,sIAAsI;CACzI;AACD;IACI,kJAA0I;YAA1I,0IAA0I;CAC7I;AACD;IACI,gJAAwI;YAAxI,wIAAwI;CAC3I;AACD;IACI,4IAAoI;YAApI,oIAAoI;CACvI;AACD;IACI,kJAA0I;YAA1I,0IAA0I;CAC7I;AACD;IACI,6IAAqI;YAArI,qIAAqI;CACxI;CACA;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAED,iBAAiB;AAEjB;IACI,oBAAoB;IACpB,eAAe;CAClB;ACqDD;EACA,WAAA;CACA","file":"Register.vue","sourcesContent":["/* ---------- Global Styling ---------- */\n\nbody {\n    font: 18px 'Roboto';\n    background: linear-gradient(to bottom right, #FFFFFF 0%,  #D8D4DC 100%);\n    background-size: cover;\n    text-align:center;\n    padding-top: 30px;\n    min-height: 100vh; \n}\n\na, p {\n    text-align:center;\n    font: 14px 'Roboto';\n    color: #29105b;\n}\n\nh1 {\n    font: 28px 'Roboto';\n    color: #29105b;\n    text-align:center;\n}\n\n#form-footer{\n    padding-bottom:5px;\n    padding-top:15px;\n    text-align: center;\n}\n#form-footer p {\n    text-align: center;\n    font: 12px 'Roboto';\n    color: rgb(129, 114, 114);\n}\n\n\n/* Card Formatting */\n\n.card {\n    margin-bottom: 0px;\n    max-width:500px;\n    margin-left: auto;\n    margin-right:auto;\n    border-radius: 10px;\n}\n\n/* .card #rounded {\n    border-radius: 10px;\n} */\n\n\n/* Container Formatting */\n\n.container#center {\n    /* margin-left: 0 auto;  */\n    /* shorthand for:\n    margin-top: 0;\n    margin-bottom: 0;\n    margin-left: auto;\n    margin-right: auto; */\n    text-align:center;\n}\n\n.container p {\n    text-align: center;\n    font: 12px 'Roboto';\n    color: rgb(129, 114, 114);\n}\n\n/* --------- Register Form---------- */\n\n\n\nform {\n    border-radius: 5px;\n    max-width:600px;\n    width:100%;\n    /* margin: 5% auto; */\n    padding: 5px 15px;\n    background-color: #FFFFFF;\n    overflow: hidden; \n    border-radius: 25px;\n}\n\n.center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 100%;\n    height:auto;\n    animation-name: moveInLeft;\n    animation-duration: 2s;\n}\n\n#register{\n    border-radius: 10px;\n}\n\n#register, label{\n    font: 18px 'Roboto';\n    color: rgb(94, 94, 94);\n    text-align:left;\n}\n\n#personalinfo {\n    width: 49.5%; \n    float:left;\n    border-right: 1px dotted #CCC;\n    box-sizing: border-box;\n    padding: 0px 15px 0px 0px;\n    overflow: hidden;\n    clear: both;\n}\n  \n#companyinfo {\n    width:49.5%;\n    float:right;\n    box-sizing: border-box;\n    padding: 0px 0px 0px 15px;\n    overflow: hidden;\n    clear: both;\n}\n\n#register-form-footer{\n    border-top: 2px #917e9e;\n}\n\n\n.fixed-horizontal-center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 50%;  \n}\n\n.error {\n    color: red;\n}\n\n\np {\n    text-align: left;\n    color: white;\n    /* font-size: 15px */\n    font: 20px \"roboto\";\n}\n\n#btn-login {\n