;;;;IAyBI;ACxQJ;EACA,0BAAA;EACA,aAAA;CACA;AACA;EACA,kBAAA;EACA,mBAAA;CACA;AACA;EACA,oBAAA;EACA,wBAAA;CACA;AACA;EACA,oBAAA;EACA,wBAAA;CACA","file":"PublicProfile.vue","sourcesContent":["body.dashboard {\n  /* opacity: 0.50; */\n  text-align: justify;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  min-height: 100vh;\n  padding: 15px 80px;\n  /* background: linear-gradient(to bottom right, #ffffff 0%, #d8d4dc 100%); */\n  background: linear-gradient(to bottom right, #ffffff 0%, #c7c4d1 100%);\n}\n\n.expand-enter-active,\n.expand-leave-active {\n  transition: height 1s ease-in-out;\n  overflow: hidden;\n}\n\n.expand-enter,\n.expand-leave-to {\n  height: 0;\n}\n\n\n#dashboard #name-location {\n  max-width: 1314px;\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n  background: linear-gradient( white 60%, #311c63 40%);\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  padding-top: 13px;\n  padding-bottom:4px;\n  border-radius: 5px;\n}\n\n#dashboard #service-card {\n  border-bottom: 2px solid #4527a0;\n  /* color: #5c5d60; */\n}\n\n#dashboard #stats {\n  background-color: white;\n  box-shadow: 2px 2px 5px 1px  #bebbc7;\n  padding: 5px;\n  border-radius: 5px;\n}\n\n#stats .card {\n  border-color: yellowgreen;\n  border-width: 3px; \n  border-style: solid;\n  border-radius: 5px;\n  margin-left:5px;\n  margin-right:5px;\n}\n\n.dashboard #about {\n  display: inline-block;\n  padding-left: 30px;\n  padding-top: 10px;\n  background-color: white;\n  border-width: .5px;\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  /* max-width: 1314px; */\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n  border-radius: 5px;\n}\n\n.dashboard #services {\n  padding-left: 20px;\n  padding-right: 20px;\n  padding-top: 0px;\n  background-color: white ;\n  color:black;\n  box-shadow: 1px 1px 2px 1px  #9f9aac;\n  border-radius: 5px;\n}\n\n/* .dashboard .service-block{\n  background-color: #4a148c;\n  margin-top:10px;\n  border-radius: 5px;\n} */\n\n.dashboard .service-block-sub{\n  padding-left:30px;\n  border-radius: 5px;\n}\n\n/* .dashboard .service-block #service-title{\n  background-color: #d6d1e4;\n  box-shadow:  0px 2px 2px 1px  #c2bece;\n  padding-left: 10px;\n  padding-top:0px;\n  border-radius: 5px;\n} */\n\n.dashboard .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n.dashboard .service-block-sub #service-title{\n  background-color: #4a148c;\n  padding-left: 10px;\n  margin-right: 10px;\n  padding-top:0px;\n  border-radius: 5px;\n  box-shadow:0px 0px 0px 0px;\n}\n\n.dashboard .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n/* Profile photo - company logo */\n.circle,\n#logo {\n  background-color: white;\n  width: 150px;\n  height: 150px;\n  -moz-border-radius: 75px;\n  -webkit-border-radius: 75px;\n  border-radius: 75px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-image: url(\"./Icons/noun_DNA_2362091.png\");\n}\n\n#dashboard #logo-border {\n  background-color: #22104b;\n  width: 170px;\n  height: 170px;\n  -moz-border-radius: 85px;\n  -webkit-border-radius: 85px;\n  border-radius: 85px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 10px;\n}\n\n#price-chart {\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  max-width: 800px;\n}\n\n/*------------- Text ------------------------*/\n.dashboard #dashboard #add-service p {\n  font-size: 15px;\n  text-align: left;\n}\n\n#dashboard .md-title {\n  font-size: 25px;\n  color: #4527a0;\n}\n\n#dashboard #service_logo {\n  color: #4527a0;\n  font-size: 25px;\n}\n\n.dashboard #dashboard h1 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 35px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard #dashboard h3 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard .services h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block-sub h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n#dashboard #about h4 {\n  /* font-style: medium; */\n  font: 19px \"Roboto\";\n  color: black;\n  text-align: center;\n}\n\n#dashboard #about p {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 17px \"Roboto\";\n  color: black;\n  text-align: center;\n\n}\n\n.dashboard #dashboard #stats h4 {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 18px \"Roboto\";\n  color: black;\n  text-align: center;\n}\n\n#dashboard #service-desc h5 {\n  font: 15px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #services h6 {\n  font: 17px \"Roboto\";\n  color: white;\n}\n\n#dashboard #stats h6 {\n  font: 15px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n.card-text {\n  font: 18px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard p {\n  font: 20px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard #price-chart p  {\n  font: 16px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #spec_inquiry p {\n  text-align: left;\n}\n\n/* Responsive Text */\n\na {\n  color: #311c63;\n  -webkit-transition: all 0.35s;\n  transition: all 0.35s;\n}\n\na:hover,\na:focus {\n  color: #6a2c86;\n}\n\nhr {\n  max-width: 100px;\n  margin: 25px auto 0;\n  border-width: 1px;\n  border-color: rgba(34, 34, 34, 0.1);\n}\n\np {\n  font-size: 18px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n  margin-bottom: 20px;\n}\n\n.form p {\n  font-size: 12px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n}\n\n.expand-enter-active,\n.expand-leave-active {\n  transition-property: opacity, height;\n}\n\n.expand-enter,\n.expand-leave-to {\n  opacity: 0;\n}\n\n/* ------------- buttons ----------------- */\n/* The following button css was copied directly from FigAnal for reference */\n\n/* Stats View Leads */\n\n.btn-quote-req{\n  border-radius: 0px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-gen-inq {\n  border-radius: 0px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n  background-color: #2238ff;\n}\n\n/* Stats Add credits */\n/* .btn-add-service {\n  border-radius: 300px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-login {\n  border-radius: 300px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-signup-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-login-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-signup-outline:hover, .btn-signup-outline:focus, .btn-signup-outline.active {\n  color: white;\n  border-color: #361555;\n  background-color: #361555;\n}\n\n.btn-login-outline:hover, .btn-login-outline:focus, .btn-login-outline.active {\n  color: white;\n  border-color: #088a12;\n  background-color: #088a12;\n}\n\n.btn-xl {\nfont-size: 11px;\npadding: 15px 45px;\n} */\n\n\n\n\n/* Tagging Basic Style */\n\n\n\n\n/* ------Vue Directive Responsive Design Ish ----------------------------------------------------*/\n\n.responsive-component.small body.dashboard {\n  /* opacity: 0.50; */\n  text-align: center;\n  text-align: justify;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  min-height: 100vh;\n  padding: 15px 0px;\n  /* background-image: url(\"./BACKGROUND.png\"); */\n  background: linear-gradient(to bottom right, #ffffff 0%, #d8d4dc 100%);\n} \n\n\n/* .responsive-component.small #name-location {\n  background: linear-gradient( white 70%, #311c63 30%);\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n}  */\n\n\n\n/* .responsive-component #dashboard #company-name {\n\n  color: white;\n\n}\n\n.responsive-component #dashboard #company-location{\n  color: white;\n\n\n} */\n\n\n\n/* logo */\n.responsive-component.small #dashboard #logo {\n  background-color: white;\n  width: 100px;\n  height: 100px;\n  -moz-border-radius: 50px;\n  -webkit-border-radius: 50px;\n  border-radius: 50px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-image: url(\"./Icons/noun_DNA_2362091.png\");\n}\n\n\n.responsive-component.small #dashboard #logo-border {\n  background-color: #22104b;\n  width: 120px;\n  height: 120px;\n  -moz-border-radius: 60px;\n  -webkit-border-radius: 60px;\n  border-radius: 60px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 10px;\n}\n\n/* .responsive-component.small #company-name {\n\n  color: white;\n} */\n\n/* .responsive-component.small #dashboard #company-location{\n  padding-top: 20px;\n} */\n\n/* text */\n\n/* .responsive-component.small h1, h2 {\n  letter-spacing: 1px;\n  font-size: 28px;\n  text-align:center;\n}\n\n.responsive-component.small h3 {\n  letter-spacing: 1px;\n  font-size: 25px;\n  text-align:center;\n}\n\n.responsive-component.small p{\n  width:100%;\n  font-size: 20px;\n}\n\n.responsive-component.small footer p{\n  width:100%;\n  font-size: 10px;\n}\n\n.responsive-component.small form{\n  width:100%;\n  font-size: 12px;\n} */\n","<template>\n<div id=\"app\">\n  <buyer-header></buyer-header>\n  <div class=\"responsive-component\" v-responsive=\"{\n      small: el => el.width < 860\n      }\">\n    <body class=\"dashboard\">\n      <div class=\"container\" id=\"dashboard\">\n        <!-- <div id=\"main\"> -->\n        <div class=\"container\" id=\"name-location\">\n          <div class=\"d-flex justify-content-center\" id=\"company-name\">\n            <h1 style=\"text-align:center:\">{{ this.companyName }}</h1>\n          </div>\n          <div class=\"d-flex justify-content-center\" id=\"company-location\">\n            <h4 style=\"text-align:center:\">Location: {{ this.companyLocation }}</h4>\n          </div>\n          <div class=\"d-flex justify-content-center\" id=\"company-logo\">\n            <div id=\"logo-border\">\n              <div class=\"circle\" id=\"logo\"></div>\n            </div>\n          </div>\n        </div>\n\n        <!-- About Section -->\n        <div class=\"container\" id=\"about\">\n          <div class=\"col-md-12\">\n            <h4>About:</h4>\n            <transition-expand>\n              <md-card>\n                <div v-if=\"expanded\">{{this.about}}</div>\n              </md-card>\n            </transition-expand>\n            <md-button\n              class=\"md-dense md-raised md-primary\"\n              @click=\"expanded = !expanded\"\n            >{{ expanded ? `Shrink` : `Expand` }}</md-button>\n          </div>\n        </div>\n        <!-- </div> -->\n\n        <br />\n        <br />\n        <br />\n\n        <div class=\"container services\" id=\"services\">\n          <br />\n          <md-button\n            @click=\"manifestModalInquiry(service)\"\n            class=\"md-raised md-primary pull-right submit-buttons-md\"\n          >General inquiry</md-button>\n          <h4 style=\"color: #212529;\">Services:</h4>\n          <br />\n          <br />\n          <div class=\"col-md-9\">\n            <!-- This is where I list the services -->\n            <!-- <div >\n              \n            </div>-->\n\n            <div v-for=\"(service, index) in this.services\" :key=\"service.id\">\n              <md-card class=\"md-card-example\" style=\"background-color: white; color: #311c63\">\n                <md-card-content>\n                  <!-- <div class=\"card\" v-if=\"(service.isSubService === false)\"></div> -->\n\n                  <div v-if=\"(service.isSubService === false)\">\n                    <h4\n                      class=\"card-header\"\n                      style=\"text-align:left; text-indent:15px; color: black\"\n                    >Title: {{ service.title }}</h4>\n\n                    <div class=\"card-body\">\n                      <!-- Service Description -->\n                      <md-button\n                        @click=\"manifestModalForm(service)\"\n                        class=\"md-raised md-primary pull-right\"\n                        style=\"background-color: #2238ff; color: white;\"\n                      >Request Quote</md-button>\n\n                      <h5\n                        class=\"card-text\"\n                        style=\"text-align:left; padding:15px;\"\n                      >Description: {{ service.description }}</h5>\n\n                      <!-- This is where I'll list the subservices -->\n\n                      <md-card\n                        style=\"width: 80%; margin-left: auto; margin-right: auto; background-color: #d6d1e4\"\n                        v-for=\"(subService, index) in services\"\n                        :key=\"index\"\n                      >\n                        <div\n                          class=\"container\"\n                          v-if=\"(subService.parentServiceId === service.id) && (subService.isSubService === true)\"\n                        >\n                          <table class=\"table table-hover\">\n                            <thead>\n                              <tr>\n                                <th scope=\"col\">Service Subtitle</th>\n                                <th scope=\"col\">Turn Around Time</th>\n                                <th scope=\"col\">Price/ Unit</th>\n                              </tr>\n                            </thead>\n                            <tbody>\n                              <tr>\n                                <th scope=\"row\">{{ subService.title }}</th>\n                                <td>{{ subService.turnAroundTime }}</td>\n                                <td>{{ subService.description }}</td>\n                                <td>\n                                  <md-button\n                                    style=\"background-color: #28a745; color: white;\"\n                                    @click=\"addServiceToCart(subService, index)\"\n                                  >Add to Cart</md-button>\n                                </td>\n                              </tr>\n                            </tbody>\n                          </table>\n                        </div>\n                      </md-card>\n                      <!--  -->\n\n                      <!-- Service/Price Listings -->\n\n                      <md-button\n                        style=\"background-color: #28a745; color: white;\"\n                        v-if=\"subServicesPresent(service) === false\"\n                        @click=\"addServiceToCart(service, index)\"\n                      >Add to Cart</md-button>\n                    </div>\n                  </div>\n                </md-card-content>\n              </md-card>\n            </div>\n\n            <!-- This is where I list the services -->\n          </div>\n\n          <!-- quote request cart -->\n          <div class=\"col-md-3\">\n            <md-card>\n              <request-quote-cart></request-quote-cart>\n            </md-card>\n          </div>\n        </div>\n      </div>\n\n      <br />\n      <br />\n      <br />\n      <br />\n      <br />\n      <br />\n    </body>\n\n    <!-- General Inquiry -->\n    <modal name=\"general-inquiry-modal\">\n      <div class=\"container\" style=\"margin:50px\" id=\"gen_inquiry\">\n        <div class=\"row\">\n          <div class=\"col-10\">\n            <p>Want to send a message to this seller about their listing?</p>\n            <textarea\n              v-model=\"inquiryText\"\n              class=\"form-control animated\"\n              placeholder=\"Enter your message\"\n              rows=\"5\"\n            ></textarea>\n\n            <button\n              class=\"btn btn-info pull-right\"\n              @click=\"submitInquiryText()\"\n              style=\"margin-top:10px\"\n              type=\"button\"\n            >Submit</button>\n          </div>\n        </div>\n      </div>\n    </modal>\n\n    <!-- Specific Inquiry -->\n    <modal height=\"auto\" scrollable=\"true\" name=\"request-quote-modal\" style=\"padding-bottom:25px\">\n      <div class=\"container\" style=\"margin:25px\" id=\"spec_inquiry\">\n        <div class=\"row\">\n          <div class=\"col-10\">\n            <div v-if=\"this.itemChosen\">\n              <p style=\"text-align:left;\">\n                <strong>Service : {{ this.itemChosen.title }}</strong>\n              </p>\n              <ul>\n                <!-- price/unit -->\n                <li>Price/Unit</li>\n\n                <!-- turnaround time -->\n                <li>Turnaround Time</li>\n              </ul>\n\n              <label>Amount? (optional)</label>\n              <input v-model=\"pickedQuantityQuoteRequest\" type=\"number\" style=\"width: 40px\" />\n\n              <br />\n              <br />Send message to Seller\n              <textarea\n                v-model=\"inquiryText\"\n                class=\"form-control animated\"\n                placeholder=\"Enter your message\"\n                rows=\"5\"\n              ></textarea>\n            </div>\n          </div>\n          <br />\n        </div>\n\n        <md-button\n          class=\"md-raised md-primary submit-buttons-md\"\n          style=\"margin-top:10px\"\n          type=\"button\"\n          @click=\"submitQuoteRequest()\"\n        >Submit</md-button>\n      </div>\n    </modal>\n  </div>\n</div>\n\n<!-- Potentially deprecated -->\n</template>\n\n\n<script src=\"./scripts/publicProfile.js\">\n</script>\n\n<style scoped>\n@import \"../assets/css/dashboard.css\";\n@import url(\"https://fonts.googleapis.com/css?family=Lato|Roboto\");\n.submit-buttons-md {\n  background-color: #2238ff;\n  color: white;\n}\ndiv.card {\n  margin-left: auto;\n  margin-right: auto;\n}\n#shopping-cart {\n  margin-left: 1000px;\n  border: 1px solid green;\n}\n#request-quote-cart {\n  margin-left: 1000px;\n  border: 1px solid green;\n}\n</style>\n\n\n"],"sourceRoot":""}]);

