ices h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block-sub h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n#dashboard #about h4 {\n  /* font-style: medium; */\n  font: 19px \"Roboto\";\n  color: black;\n  text-align: center;\n}\n\n#dashboard #about p {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 17px \"Roboto\";\n  color: black;\n  text-align: center;\n\n}\n\n.dashboard #dashboard #stats h4 {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 18px \"Roboto\";\n  color: black;\n  text-align: center;\n}\n\n#dashboard #service-desc h5 {\n  font: 15px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #services h6 {\n  font: 17px \"Roboto\";\n  color: white;\n}\n\n#dashboard #stats h6 {\n  font: 15px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n.card-text {\n  font: 18px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard p {\n  font: 20px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard #price-chart p  {\n  font: 16px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #spec_inquiry p {\n  text-align: left;\n}\n\n/* Responsive Text */\n\na {\n  color: #311c63;\n  -webkit-transition: all 0.35s;\n  transition: all 0.35s;\n}\n\na:hover,\na:focus {\n  color: #6a2c86;\n}\n\nhr {\n  max-width: 100px;\n  margin: 25px auto 0;\n  border-width: 1px;\n  border-color: rgba(34, 34, 34, 0.1);\n}\n\np {\n  font-size: 18px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n  margin-bottom: 20px;\n}\n\n.form p {\n  font-size: 12px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n}\n\n.expand-enter-active,\n.expand-leave-active {\n  transition-property: opacity, height;\n}\n\n.expand-enter,\n.expand-leave-to {\n  opacity: 0;\n}\n\n/* ------------- buttons ----------------- */\n/* The following button css was copied directly from FigAnal for reference */\n\n/* Stats View Leads */\n\n.btn-quote-req{\n  border-radius: 0px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-gen-inq {\n  border-radius: 0px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n  background-color: #2238ff;\n}\n\n/* Stats Add credits */\n/* .btn-add-service {\n  border-radius: 300px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-login {\n  border-radius: 300px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-signup-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-login-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-signup-outline:hover, .btn-signup-outline:focus, .btn-signup-outline.active {\n  color: white;\n  border-color: #361555;\n  background-color: #361555;\n}\n\n.btn-login-outline:hover, .btn-login-outline:focus, .btn-login-outline.active {\n  color: white;\n  border-color: #088a12;\n  background-color: #088a12;\n}\n\n.btn-xl {\nfont-size: 11px;\npadding: 15px 45px;\n} */\n\n\n\n\n/* Tagging Basic Style */\n\n\n\n\n/* ------Vue Directive Responsive Design Ish ----------------------------------------------------*/\n\n.responsive-component.small body.dashboard {\n  /* opacity: 0.50; */\n  text-align: center;\n  text-align: justify;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  min-height: 100vh;\n  padding: 15px 0px;\n  /* background-image: url(\"./BACKGROUND.png\"); */\n  background: linear-gradient(to bottom right, #ffffff 0%, #d8d4dc 100%);\n} \n\n\n/* .responsive-component.small #name-location {\n  background: linear-gradient( white 70%, #311c63 30%);\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n}  */\n\n\n\n/* .responsive-component #dashboard #company-name {\n\n  color: white;\n\n}\n\n.responsive-component #dashboard #company-location{\n  color: white;\n\n\n} */\n\n\n\n/* logo */\n.responsive-component.small #dashboard #logo {\n  background-color: white;\n  width: 100px;\n  height: 100px;\n  -moz-border-radius: 50px;\n  -webkit-border-radius: 50px;\n  border-radius: 50px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-image: url(\"./Icons/noun_DNA_2362091.png\");\n}\n\n\n.responsive-component.small #dashboard #logo-border {\n  background-color: #22104b;\n  width: 120px;\n  height: 120px;\n  -moz-border-radius: 60px;\n  -webkit-border-radius: 60px;\n  border-radius: 60px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 10px;\n}\n\n/* .responsive-component.small #company-name {\n\n  color: white;\n} */\n\n/* .responsive-component.small #dashboard #company-location{\n  padding-top: 20px;\n} */\n\n/* text */\n\n/* .responsive-component.small h1, h2 {\n  letter-spacing: 1px;\n  font-size: 28px;\n  text-align:center;\n}\n\n.responsive-component.small h3 {\n  letter-spacing: 1px;\n  font-size: 25px;\n  text-align:center;\n}\n\n.responsive-component.small p{\n  width:100%;\n  font-size: 20px;\n}\n\n.responsive-component.small footer p{\n  width:100%;\n  font-size: 10px;\n}\n\n.responsive-component.small form{\n  width:100%;\n  font-size: 12px;\n} */\n","<template>\n<div id=\"app\">\n  <dashboard-header />\n  <body class=\"dashboard\">\n    <!--  -->\n\n    <div\n      class=\"payment-cards\"\n      v-for=\"source in this.customerSources\"\n      v-bind:key=\"source.id\"\n      v-on:click=\"promptForDefaultSourceConfirm(source.id)\"\n    >\n      <pre>{{ source.brand }} ending in\t{{ source.last4 }} </pre>\n      <pre> expires : {{ source.exp_month }} / {{ source.exp_year }}</pre>\n    </div>\n\n    <!--  -->\n  </body>\n\n  <!-- Prompt to ask the user if they want to make this card the default source -->\n  <modal name=\"change-default-source-prompt\">\n    <div class=\"container\" style=\"margin:75px\">\n      <div class=\"row\">\n        <div class=\"col-md-4\">\n          <h4>Would you like to make this card your default payment method ?</h4>\n\n          <button\n            class=\"btn btn-info pull-right\"\n            style=\"margin-top:10px\"\n            type=\"button\"\n            @click=\"makeDefaultSource\"\n          >Submit</button>\n        </div>\n      </div>\n    </div>\n  </modal>\n  <!--  -->\n\n  <!-- Tells the user that the default source has been changed -->\n  <modal name=\"default-source-changed\">\n    <div class=\"container\" style=\"margin:75px\">\n      <div class=\"row\">\n        <div class=\"col-md-4\">\n          <h4>Your default payment method has been changed</h4>\n\n          <button\n            class=\"btn btn-info pull-right\"\n            style=\"margin-top:10px\"\n            type=\"button\"\n            @click=\"hideConfirmMessage\"\n          >OK Great</button>\n        </div>\n      </div>\n    </div>\n  </modal>\n  <!--  -->\n</div>\n</template>\n\n<script>\nimport DashboardHeader from \"@/components/DashboardHeader.vue\";\nimport SettingsService from \"@/services/SettingsService\";\n\nexport default {\n  data() {\n    return {\n      user: null,\n      customerSources: [],\n      sourceToBeDefaulted: \"\"\n    };\n  },\n  mounted() {\n    this.getStripeUserInfo();\n  },\n  components: {\n    DashboardHeader\n  },\n  methods: {\n    // Takes the source ID provided and sets it as the default source\n    async hideConfirmMessage() {\n      this.$modal.hide(\"default-source-changed\");\n    },\n    async makeDefaultSource() {\n      try {\n        const userExtracted = this.$store.getters.getUserInfo;\n\n        const updateDefaultSourceInfo = {\n          stripeCustomerId: userExtracted.stripeCustomerId,\n          sourceID: this.sourceToBeDefaulted\n        };\n        this.$modal.hide(\"change-default-source-prompt\");\n        const response = await SettingsService.updateDefaultSource(\n          updateDefaultSourceInfo\n        );\n        console.log(`\\nResponse for makeDefaultSource : ${response}\\n`);\n        this.$modal.show(\"default-source-changed\");\n      } catch (error) {\n        console.log(`\\nThe error inside of makeDefaultSource: ${error}`); // TESTING\n        if (error) throw error;\n      }\n    },\n    async promptForDefaultSourceConfirm(sourceID) {\n      try {\n        this.$modal.show(\"change-default-source-prompt\");\n        this.sourceToBeDefaulted = sourceID;\n      } catch (error) {\n        console.log(\n          `\\nAn error occurred in promptForDefaultSourceConfirm ${error}\\n`\n        );\n        if (error) throw error;\n      }\n    },\n    async getStripeUserInfo() {\n      try {\n        const userExtracted = this.$store.getters.getUserInfo;\n        const stripeUserInfo = await SettingsService.getStripeUserInfo(\n          userExtracted\n        );\n        this.subscriptions = stripeUserInfo.data.subscriptions.data;\n        const customerSources = stripeUserInfo.data.stripeCustomer.sources;\n        this.customerSources = customerSources.data;\n\n        console.log(\n          `\\nThe customer's default source is : ${JSON.stringify(\n            stripeUserInfo.data.stripeCustomer.default_source\n          )}\\n`\n        ); // TESTING\n      } catch (error) {\n        console.log(\n          `\\nAn error occurred in SettingsChangeDefaultSource getStripeUserInfo ${error}\\n`\n        );\n        if (error) throw error;\n      }\n    }\n  }\n};\n</script>\n\n<style>\n@import \"../assets/css/dashboard.css\";\n.payment-cards {\n  width: 400px;\n  font-size: 150%;\n  border: 2px solid purple;\n  padding: 20px;\n  margin-left: 10px;\n}\n</style>\n"],"sourceRoot":""}]);

