enter;\n  display: inline-block;\n  border-radius: 5px;\n  font-size:18px;\n}\n\n#btn-shippo:hover {\n  color: white;\n  border-color: rgb(121, 161, 39);\n  background-color: rgb(121, 161, 39);\n  border-width: 2px;\n  justify-content: center;\n  display: inline-block;\n  border-radius: 5px;\n  font-size:18px;\n}\n\n\n/* Current Orders Timeline */\n#seller-timeline {\n  list-style: none;\n  position: relative;\n  margin-left:10%;\n  margin-right: 10%;\n}\n#seller-timeline:before {\n  top: 0;\n  bottom: 0;\n  position: absolute;\n  content: \" \";\n  width: 5px;\n  /* background-color: #4997cd; */\n  background: rgb(0, 132, 255);\n  background: linear-gradient(rgb(0, 132, 255)  35%, rgb(128,0,128) 75%);\n  /* background-color: linear-gradient(rgba(140,10,196,1) 35%, rgba(167,255,0,1));   */\n  left: 50%;\n  margin-left: -1.5px;\n}\n#seller-timeline .clearFix {\n  clear: both;\n  height: 0;\n}\n#seller-timeline .timeline-badge {\n  color: #fff;\n  width: 60px;\n  height: 60px;\n  font-size: 1.2em;\n  text-align: center;\n  position: absolute;\n  top: 30%;\n  left: 50%;\n  margin-left: -30px;\n  z-index: 100;\n  border-top-right-radius: 50%;\n  border-top-left-radius: 50%;\n  border-bottom-right-radius: 50%;\n  border-bottom-left-radius: 50%;\n}\n\n\n\n#seller-timeline .timeline-badge-small {\n  color: #fff;\n  width: 30px;\n  height: 30px;\n  font-size: 1.2em;\n  text-align: center;\n  position: absolute;\n  top: 30%;\n  left: 50%;\n  margin-left: -15px;\n  z-index: 100;\n  border-top-right-radius: 50%;\n  border-top-left-radius: 50%;\n  border-bottom-right-radius: 50%;\n  border-bottom-left-radius: 50%;\n}\n\n#status-complete {\n  background-color: #88cc00;\n}\n\n#status-incomplete {\n  background-color: grey;\n  opacity: 20%;\n}\n\n@keyframes glowing {\n  0% { box-shadow: 0 0 -10px rgb(156, 0, 156); }\n  40% { box-shadow: 0 0 20px rgb(156, 0, 156); }\n  60% { box-shadow: 0 0 20px rgb(156, 0, 156); }\n  100% { box-shadow: 0 0 -10px rgb(156, 0, 156); }\n}\n\n.badge-glow {\n  animation: glowing 3500ms infinite;\n}\n\n#seller-timeline .timeline-badge.timeline-filter-movement a span {\n  color: #4997cd;\n  font-size: 1.3em;\n  top: -1px;\n}\n#seller-timeline .timeline-badge.timeline-future-movement {\n  background-color: #ffffff;\n  height: 35px;\n  width: 35px;\n  font-size: 1.7em;\n  top: -16px;\n  margin-left: -18px;\n}\n#seller-timeline .timeline-badge.timeline-future-movement a span {\n  color: #4997cd;\n  font-size: .9em;\n  top: 2px;\n  left: 1px;\n}\n#seller-timeline .timeline-movement {\n  /* border-bottom: dashed 1px grey; */\n  position: relative;\n}\n#seller-timeline .timeline-movement.timeline-movement-top {\n  height: 60px;\n}\n#seller-timeline .timeline-movement .timeline-item {\n  padding: 20px 0;\n}\n#seller-timeline .timeline-movement .timeline-item .timeline-panel.seller-side {\n  border: 1px solid #d4d4d4;\n  border-radius: 3px;\n  /* background-color: #FFFFFF; */\n  background-color: rgb(241, 241, 241);\n  color: #666;\n  padding: 10px;\n  position: relative;\n  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);\n  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);\n}\n\n#seller-timeline .timeline-movement .timeline-item .timeline-panel.buyer-side {\n  border: 1px solid #d4d4d4;\n  border-radius: 3px;\n  background-color: #FFFFFF;\n  /* background-color: rgb(239, 236, 241); */\n  color: #666;\n  padding: 10px;\n  position: relative;\n  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);\n  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);\n}\n\n#seller-timeline .timeline-movement .timeline-item .timeline-panel .timeline-panel-ul {\n  list-style: none;\n  padding: 0;\n  margin: 0;\n}\n#seller-timeline .timeline-movement .timeline-item .timeline-panel.seller-side .timeline-panel-ul {\n  text-align: right;\n}\n#seller-timeline .timeline-movement .timeline-item .timeline-panel.seller-side .timeline-panel-ul li {\n  color: #666;\n  font-size: 15px;\n}\n#seller-timeline .timeline-movement .timeline-item .timeline-panel.buyer-side .timeline-panel-ul li {\n  color: #666;\n  font-size: 15px;\n}\n#seller-timeline .timeline-movement .timeline-item .timeline-panel.seller-side .timeline-panel-ul li span.importo {\n  color: #8524b3;\n  font-size: 17px;\n}\n#seller-timeline .timeline-movement .timeline-item .timeline-panel.buyer-side .timeline-panel-ul {\n  text-align: left;\n}\n#seller-timeline .timeline-movement .timeline-item .timeline-panel.buyer-side .timeline-panel-ul span.importo {\n  color: #468c1f;\n  font-size: 17px;\n}\n/* Order History timeline */\n\nul.oh_timeline {\n  list-style-type: none;\n  position: relative;\n}\n\nul.oh_timeline:before {\n  content: ' ';\n  background: #31037a;\n  display: inline-block;\n  position: absolute;\n  left: 29px;\n  width: 2px;\n  height: 100%;\n  z-index: 400;\n}\nul.oh_timeline > li {\n  margin: 20px 0;\n  padding-left: 20px;\n}\nul.oh_timeline > li:before {\n  content: ' ';\n  background: white;\n  display: inline-block;\n  position: absolute;\n  border-radius: 50%;\n  border: 3px solid #31037a;\n  left: 20px;\n  width: 20px;\n  height: 20px;\n  z-index: 400;\n}\n\n\n/* ------------ Buyer DASHBOARD----------- */\n\nbody.orders {\n  padding-top: 60px;\n  padding-bottom: 100px;\n  text-align:left;\n  min-width: 100%;\n  background: linear-gradient(to bottom right, #FFFFFF 0%,  #D8D4DC 100%);\n  position: relative;\n  /* background: white; */\n  /* background: linear-gradient(to bottom right, rgb(86, 0, 122) 0%,  rgb(33, 0, 63) 100%); */\n  min-height: 100vh;  \n}\n\n#shippo-ui {\n  background: #88cc00;  \n}\n\n.page-header {\n  padding-left: 20px;\n  padding-right: 20px;\n  /* max-width: 1200px; */\n  position: relative;\n}\n\n.current-order-status {\n  position: relative;\n  max-width: 1200px;\n}\n\n#orders-main {\n  border-bottom: 2px solid #8e8b91;\n  max-width: 500px;\n}\n\n#shipping-main {\n  max-width: 500px;\n}\n\n.orders h3 {\n  letter-spacing: 1px;\n  font: 20px 'Roboto';\n  color: #29105b;\n  text-align:left;\n}\n\n#company-name h4 {\n  letter-spacing: 1px;\n  text-transform: uppercase;\n  font: 18px 'Roboto';\n  color: #6f6d72;\n  font-style: 'bold';\n  text-align:left;\n}\n\n#orders-main h5 {\n  letter-spacing: 1px;\n  font: 25px 'Roboto';\n  color: #29105b;\n  text-align:left;\n}\n\n#order-history h6 {\n  letter-spacing: 1px;\n  font: 20px 'Roboto';\n  color: #29105b;\n  text-align:left;\n  text-transform: uppercase;\n  /* text-shadow: 2px 2px 4px #D8D4DC;  */\n}\n\n#order-history p {\n  font: 16px 'Roboto';\n  color: #29105b;\n}\n\n.orders a {\n  color: #6a2c86;\n  -webkit-transition: all 0.35s;\n  transition: all 0.35s;\n}\n\n.orders a:hover,\na:focus {\n  color: #29105b\n}\n\n/* Order History Timeline */\n/* #payment-history {\n  padding-left: 20px;\n  padding-top: 20px;\n}\nul.timeline {\n  list-style-type: none;\n  position: relative;\n}\nul.timeline:before {\n  content: ' ';\n  background: #31037a;\n  display: inline-block;\n  position: absolute;\n  left: 29px;\n  width: 2px;\n  height: 100%;\n  z-index: 400;\n}\nul.timeline > li {\n  margin: 20px 0;\n  padding-left: 20px;\n}\nul.timeline > li:before {\n  content: ' ';\n  background: white;\n  display: inline-block;\n  position: absolute;\n  border-radius: 50%;\n  border: 3px solid #31037a;\n  left: 20px;\n  width: 20px;\n  height: 20px;\n  z-index: 400;\n} */\n\n/* Orders Responsive ish */\n\n.responsive-component.small body.orders {\n  padding-top: 85px;\n  padding-left: 10px;\n  padding-bottom: 100px;\n  text-align:left;\n  background: linear-gradient(to bottom right, #FFFFFF 0%,  #D8D4DC 100%);\n  min-height: 100vh;  \n}\n\n.responsive-component.small #orders {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 100%;  \n}","body.dashboard {\n  /* opacity: 0.50; */\n  text-align: justify;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  min-height: 100vh;\n  padding: 15px 80px;\n  /* background: linear-gradient(to bottom right, #ffffff 0%, #d8d4dc 100%); */\n  background: linear-gradient(to bottom right, #ffffff 0%, #c7c4d1 100%);\n}\n\n.expand-enter-active,\n.expand-leave-active {\n  transition: height 1s ease-in-out;\n  overflow: hidden;\n}\n\n.expand-enter,\n.expand-leave-to {\n  height: 0;\n}\n\n\n#dashboard #name-location {\n  max-width: 1314px;\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n  background: linear-gradient( white 60%, #311c63 40%);\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  padding-top: 13px;\n  padding-bottom:4px;\n  border-radius: 5px;\n}\n\n#dashboard #service-card {\n  border-bottom: 2px solid #4527a0;\n  /* color: #5c5d60; */\n}\n\n#dashboard #stats {\n  background-color: white;\n  box-shadow: 2px 2px 5px 1px  #bebbc7;\n  padding: 5px;\n  border-radius: 5px;\n}\n\n#stats .card {\n  border-color: yellowgreen;\n  border-width: 3px; \n  border-style: solid;\n  border-radius: 5px;\n  margin-left:5px;\n  margin-right:5px;\n}\n\n.dashboard #about {\n  display: inline-block;\n  padding-left: 30px;\n  padding-top: 10px;\n  background-color: white;\n  border-width: .5px;\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  /* max-width: 1314px; */\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n  border-radius: 5px;\n}\n\n.dashboard #services {\n  padding-left: 20px;\n  padding-right: 20px;\n  padding-top: 0px;\n  background-color: white ;\n  color:black;\n  box-shadow: 1px 1px 2px 1px  #9f9aac;\n  border-radius: 5px;\n}\n\n/* .dashboard .service-block{\n  background-color: #4a148c;\n  margin-top:10px;\n  border-radius: 5px;\n} */\n\n.dashboard .service-block-sub{\n  padding-left:30px;\n  border-radius: 5px;\n}\n\n/* .dashboard .service-block #service-title{\n  background-color: #d6d1e4;\n  box-shadow:  0px 2px 2px 1px  #c2bece;\n  padding-left: 10px;\n  padding-top:0px;\n  border-radius: 5px;\n} */\n\n.dashboard .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n.dashboard .service-block-sub #service-title{\n  background-color: #4a148c;\n  padding-left: 10px;\n  margin-right: 10px;\n  padding-top:0px;\n  border-radius: 5px;\n  box-shadow:0px 0px 0px 0px;\n}\n\n.dashboard .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n/* Profile photo - company logo */\n.circle,\n#logo {\n  background-color: white;\n  width: 150px;\n  height: 150px;\n  -moz-border-radius: 75px;\n  -webkit-border-radius: 75px;\n  border-radius: 75px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-image: url(\"./Icons/noun_DNA_2362091.png\");\n}\n\n#dashboard #logo-border {\n  background-color: #22104b;\n  width: 170px;\n  height: 170px;\n  -moz-border-radius: 85px;\n  -webkit-border-radius: 85px;\n  border-radius: 85px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 10px;\n}\n\n#price-chart {\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  max-width: 800px;\n}\n\n/*------------- Text ------------------------*/\n.dashboard #dashboard #add-service p {\n  font-size: 15px;\n  text-align: left;\n}\n\n#dashboard .md-title {\n  font-size: 25px;\n  color: #4527a0;\n}\n\n#dashboard #service_logo {\n  color: #4527a0;\n  font-size: 25px;\n}\n\n.dashboard #dashboard h1 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 35px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard #dashboard h3 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard .services h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block-sub h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n#dashboard #about h4 {\n  /* font-style: medium; */\n  font: 19px \"Roboto\";\n  color: black;\n  text-align: center;\n}\n\n#dashboard #about p {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 17px \"Roboto\";\n  color: black;\n  text-align: center;\n\n}\n\n.dashboard #dashboard #stats h4 {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 18px \"Roboto\";\n  color: black;\n  text-align: center;\n}\n\n#dashboard #service-desc h5 {\n  font: 15px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #services h6 {\n  font: 17px \"Roboto\";\n  color: white;\n}\n\n#dashboard #stats h6 {\n  font: 15px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n.card-text {\n  font: 18px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard p {\n  font: 20px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard #price-chart p  {\n  font: 16px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #spec_inquiry p {\n  text-align: left;\n}\n\n/* Responsive Text */\n\na {\n  color: #311c63;\n  -webkit-transition: all 0.35s;\n  transition: all 0.35s;\n}\n\na:hover,\na:focus {\n  color: #6a2c86;\n}\n\nhr {\n  max-width: 100px;\n  margin: 25px auto 0;\n  border-width: 1px;\n  border-color: rgba(34, 34, 34, 0.1);\n}\n\np {\n  font-size: 18px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n  margin-bottom: 20px;\n}\n\n.form p {\n  font-size: 12px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n}\n\n.expand-enter-active,\n.expand-leave-active {\n  transition-property: opacity, height;\n}\n\n.expand-enter,\n.expand-leave-to {\n  opacity: 0;\n}\n\n/* ------------- buttons ----------------- */\n/* The following button css was copied directly from FigAnal for reference */\n\n/* Stats View Leads */\n\n.btn-quote-req{\n  border-radius: 0px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-gen-inq {\n  border-radius: 0px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-