: \"Open Sans\", sans-serif;\n  font-size: 24px;\n  line-height: 1.5;\n  color: #2d1e35;\n  margin-bottom: 20px;\n  text-align: center;\n}\n\n.center {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 50%;\n  animation-name: moveInLeft;\n  animation-duration: 2s;\n}\n\n/* logo animation */\n@keyframes moveInLeft {\n  0% {\n    opacity: 0;\n    transform: translateX(-100px);\n  }\n\n  80% {\n    transform: translateX(20px);\n  }\n\n  100% {\n    opacity: 1;\n    transform: translate(0);\n  }\n}\n\na {\n  color: #311c63;\n  -webkit-transition: all 0.35s;\n  transition: all 0.35s;\n}\n\na:hover,\na:focus {\n  color: #6a2c86;\n}\n\nhr {\n  max-width: 100px;\n  margin: 25px auto 0;\n  border-width: 1px;\n  border-color: rgba(34, 34, 34, 0.1);\n}\n\nhr.light {\n  border-color: white;\n}\n\n.btn-signup {\n  border-radius: 300px;\n  font-family: \"Roboto\", sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-login {\n  border-radius: 300px;\n  font-family: \"Roboto\", sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-signup-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-login-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-signup-outline:hover,\n.btn-signup-outline:focus,\n.btn-signup-outline.active {\n  color: white;\n  border-color: #361555;\n  background-color: #361555;\n}\n\n.btn-login-outline:hover,\n.btn-login-outline:focus,\n.btn-login-outline.active {\n  color: white;\n  border-color: #088a12;\n  background-color: #088a12;\n}\n\n.btn-xl {\n  font-size: 11px;\n  padding: 15px 45px;\n}\n\n.icon {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 115px;\n  height: 100px;\n}\n\nsection.about{\n  padding: 150px 0px 150px;\n}\n\nsection.services {\n  padding: 100px 75px 100px;\n}\n\nsection.green {\n  padding: 0px 0px 0px 0px;\n}\n\n#pitch {\n  width: 100%;\n  max-width: 600px;\n  height:auto;\n  padding-bottom:0px;\n  padding-top:0px;\n  margin-top:0px;\n}\n\nform {\n  width: 100%;\n  font-size: 14pt;\n  display: inline-block;\n  position: center;\n}\n\n/* ----footer -----*/\n\nfooter {\n  /* margin-top:20px; */\n  padding: 0px 0px 10px;\n  text-align: left;\n  background-color: #260331;\n}\n\nfooter #contact{\n  /* background-color: rgb(190, 190, 190); */\n  background-color:#10021d;\n  /* background-color:#361555; */\n  /* background-image: linear-gradient(#4b1552,#361555); */\n  padding-top: 30px;\n}\n\nfooter #copy-rights {\n  background-color:#260331;\n  padding-top: 20px\n}\n\n#btn-footer{\n  padding:5px;\n  border-radius: 5px;\n}\n\n#contact,\np {\n  font-size: 15px;\n  margin: 0;\n  color:white;\n}\n\n#contact h4 {\n  font-size: 18px;\n  margin: 0;\n  color:white;\n}\n\nfooter .row {\n  padding: 10px;\n}\n\n.bg-primary {\n  background: #482774;\n  background: -webkit-gradient(\n    linear,\n    left top,\n    left bottom,\n    from(#4b1552),\n    to(#250d41)\n  );\n  background: linear-gradient(#4b1552, #250d41);\n}\n\n.text-primary {\n  color: #fdcc52;\n}\n\n.btn-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-outline:hover,\n.btn-outline:focus,\n.btn-outline:active,\n.btn-outline.active {\n  color: white;\n  border-color: #361555;\n  background-color: #361555;\n}\n\n.btn {\n  border-radius: 300px;\n  font-family: \"Roboto\", sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-xl {\n  font-size: 11px;\n  padding: 15px 45px;\n}\n\n.btn-default {\n  background-color: #361555;\n  color: #fff;\n  border-color: #2f3e48;\n}\n\n/* Header */\nheader.masthead {\n  position: relative;\n  width: 100%;\n  /* padding-top: 100px; */\n  padding-bottom: 300px;\n  color: white;\n  background: url(\"./HEADER.png\");\n  background: url(\"./HEADER.png\"),\n    -webkit-gradient(linear, right top, left top, from(#c7c7c7), to(#ffffff));\n  background: url(\"./HEADER.png\"), linear-gradient(to right, #e2e2e2, #ffffff);\n}\n\nul {\n  text-align: left;\n}\n\n/* ------Vue Directive Responsive Design Ish ----------------------------------------------------*/\n.responsive-component.small h1,\nh2 {\n  letter-spacing: 1px;\n  font-size: 28px;\n  text-align: center;\n}\n\n.responsive-component.small h3 {\n  letter-spacing: 1px;\n  font-size: 25px;\n  text-align: center;\n}\n\n.responsive-component.small p {\n  width: 100%;\n  font-size: 20px;\n}\n\n.responsive-component.small footer p {\n  width: 100%;\n  font-size: 10px;\n  color:white;\n}\n\n.responsive-component.small form {\n  width: 100%;\n  font-size: 12px;\n}\n\n.responsive-component.small .nav-center {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  margin-top: 10px;\n}\n","<template>\n<body>\n  <div class=\"responsive-component\" v-responsive=\"{\n    small: el => el.width < 430\n    }\">\n    <page-header/>\n    <!-- v-on:scroll=\"handleScroll\"> -->\n    <header class=\"masthead\">\n      <div class=\"col-centered my-auto\">\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <a>\n          <img src=\"./LOGO.png\" alt=\"FIG ANALYTICS\" class=\"center\">\n        </a>\n        <br>\n        <hr>\n        <br>\n        <br>\n        <h1>Grow your Biotech Network.</h1>\n        <br>\n        <br>\n        <div class=\"col-md-12 text-center\">\n          <a href=\"#start\" class=\"btn-signup btn-signup-outline btn-xl\">Sign Up</a>\n        </div>\n        <br>\n        <br>\n        <br>\n        <br>\n        <div class=\"col-md-12 text-center\">\n          <router-link class=\"btn-signup btn-signup-outline btn-xl\" to=\"/login\">Client Login</router-link>\n        </div>\n      </div>\n    </header>\n\n    <section class=\"about\">\n      <div class=\"container-fluid\">\n        <h3>\n          Email marketing on the front-end, A.I. on the back-end.\n          <br>\n        </h3>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <h4>\n          We’ve streamlined biotech marketing,\n          <br>so you can focus on innovating.\n          <br>\n        </h4>\n      </div>\n    </section>\n\n    <section class=\"services\">\n      <div class=\"col-centered my-auto\">\n        <h2>Our Services</h2>\n        <br>\n        <br>\n        <br>\n        <br>\n        <br>\n        <div class=\"container-fluid\">\n          <div class=\"row\">\n            <div class=\"col-lg-6\">\n              <div class=\"feature-item\">\n                <img src=\"../assets/css/leads.png\" class=\"icon\">\n                <h3>Automated Leads Generation</h3>\n                <h5\n                  class=\"text-muted\"\n                >Buyer discovery and matchmaking for your biotech products & services</h5>\n              </div>\n            </div>\n            <br>\n            <br>\n            <br>\n            <br>\n            <br>\n            <div class=\"col-lg-6\">\n              <div class=\"feature-item\">\n                <img src=\"../assets/css/email1.png\" class=\"icon\">\n                <h3>Email Marketing Campaigns</h3>\n                <h5\n                  class=\"text-muted\"\n                >From email design to distribution: tracked and analyzed for maximum results</h5>\n              </div>\n            </div>\n          </div>\n          <br>\n          <div class=\"row\">\n            <div class=\"col-lg-6\">\n              <div class=\"feature-item\">\n                <img src=\"../assets/css/search.png\" class=\"icon\">\n                <h3>Citation Tracking</h3>\n                <h5 class=\"text-muted\">Coming soon!</h5>\n              </div>\n            </div>\n            <div class=\"col-lg-6\">\n              <div class=\"feature-item\">\n                <img src=\"../assets/css/abstract.png\" class=\"icon\">\n                <h3>Abstract Analysis</h3>\n                <h5 class=\"text-muted\">Categorizing projects and optimizing lead generation</h5>\n              </div>\n            </div>\n          </div>\n        </div>\n      </div>\n    </section>\n\n    <section class=\"green\">\n      \n        <carousel :adjustableHeight=\"true\" :per-page=\"1\" :autoplay=\"true\" :autoplayTimeout=\"2000\">\n\n          <!-- Step 1 -->\n          <slide>\n            <img src=\"../assets/css/new_pitch_1.svg\" alt=\"FIG ANALYTICS\" class=\"center\" id=\"pitch\" >\n          </slide>\n\n          <!-- Step 2 -->\n          <slide>\n            <img src=\"../assets/css/new_pitch_2.svg\" alt=\"FIG ANALYTICS\" class=\"center\" id=\"pitch\">\n          </slide>\n\n          <!-- Step 3 -->\n          <slide>\n            <img src=\"../assets/css/new_pitch_3.svg\" alt=\"FIG ANALYTICS\" class=\"center\" id=\"pitch\">\n          </slide>\n\n          <!-- Step 4 -->\n          <slide>\n            <div class=\"container-fluid\">\n              <div class=\"col-sm-12\">\n                <h3>Start your project today.</h3>\n                <br>\n                <br>\n                <br>\n                <!-- The continue button needs to lead onto the registration around here -->\n                <form>\n                  <div class=\"row\">\n                    <div class=\"col-sm-12\">\n                      <p>Company Name:</p>\n                      <input v-model=\"companyName\" type=\"text\" name=\"company\">\n                      <br>\n                    </div>\n                  </div>\n                  <div class=\"row\">\n                    <div class=\"col-sm-12\">\n                      <p>Email:</p>\n                      <input v-model=\"emailAddress\" type=\"text\" name=\"email\">\n                    </div>\n                  </div>\n                </form>\n\n                <br>\n                <br>\n                <a\n                  href=\"#register\"\n                  class=\"btn btn-outline btn-xl\"\n                  @click=\"continueOntoRegisterForm()\"\n                >Continue</a>\n                <br>\n                <br>\n                <!-- <h3>Start your project today.</h3> -->\n              </div>\n            </div>\n          </slide>\n\n        </carousel>\n      \n    </section>\n\n    <footer>\n      <div class=\"container-fluid\" id=\"contact\">\n        <div class=\"row\">\n          <div class=\"col-lg-6\">\n            <h4 style=\"text-align:center\">Email: fig.analytica@gmail.com</h4>\n            <br>\n            <h4 style=\"text-align:center\">\n              Address: 1138 South Cleveland St,\n              <br>Oceanside CA 92054\n              <br>\n            </h4>\n            <br>\n          </div>\n          <div class=\"col-lg-2\">\n            <ul class=\"list-unstyled\">\n              <h4>\n                <li>Home</li>\n                <li>About</li>\n                <li>Services</li>\n                <li>Contact</li>\n              </h4>\n            </ul>\n          </div>\n          <br>\n          <br>\n          <br>\n          <br>\n          <div class=\"col-lg-2\">\n            <div class=\"container\" style=\"max-width:200px;text-align:center;\">\n            <router-link class=\"btn-default btn-large btn-block\" id=\"btn-footer\" to=\"/login\">Client Login</router-link>\n            <br>\n            <router-link class=\"btn-success btn-large btn-block\" id=\"btn-footer\" to=\"/register\">Sign Up</router-link>\n            </div>\n          </div>\n        </div>\n      </div>\n      <div class=\"container-fluid\" id=\"copy-rights\">\n        <p style=\"text-align:center\">Fig Analytics is a certified C Corporation.</p>\n        <p style=\"text-align:center\">&copy; FIG 2018. All Rights Reserved.</p>\n      </div>\n    </footer>\n  </div>\n</body>\n</template>\n\n<script src=\"./scripts/hello.js\">\n</script>\n\n<style scoped>\n@import \"../assets/css/figanal.css\";\n/* @import url('https://fonts.googleapis.com/css?family=Montserrat');  */\n@import url(\"https://fonts.googleapis.com/css?family=Lato|Roboto\");\n/* @import url('https://fonts.googleapis.com/css?family=Open+Sans|Roboto'); */\n</style>\n"],"sourceRoot":""}]);

