ge-small {\n  color: #fff;\n  width: 30px;\n  height: 30px;\n  font-size: 1.2em;\n  text-align: center;\n  position: absolute;\n  top: 30%;\n  left: 50%;\n  margin-left: -15px;\n  z-index: 100;\n  border-top-right-radius: 50%;\n  border-top-left-radius: 50%;\n  border-bottom-right-radius: 50%;\n  border-bottom-left-radius: 50%;\n}\n\n#status-complete {\n  background-color: #88cc00;\n}\n\n#status-incomplete {\n  background-color: grey;\n  opacity: 20%;\n}\n\n@keyframes glowing {\n  0% { box-shadow: 0 0 -10px rgb(156, 0, 156); }\n  40% { box-shadow: 0 0 20px rgb(156, 0, 156); }\n  60% { box-shadow: 0 0 20px rgb(156, 0, 156); }\n  100% { box-shadow: 0 0 -10px rgb(156, 0, 156); }\n}\n\n.badge-glow {\n  animation: glowing 3500ms infinite;\n}\n\n#seller-timeline .timeline-badge.timeline-filter-movement a span {\n  color: #4997cd;\n  font-size: 1.3em;\n  top: -1px;\n}\n#seller-timeline .timeline-badge.timeline-future-movement {\n  background-color: #ffffff;\n  height: 35px;\n  width: 35px;\n  font-size: 1.7em;\n  top: -16px;\n  margin-left: -18px;\n}\n#seller-timeline .timeline-badge.timeline-future-movement a span {\n  color: #4997cd;\n  font-size: .9em;\n  top: 2px;\n  left: 1px;\n}\n#seller-timeline .timeline-movement {\n  /* border-bottom: dashed 1px grey; */\n  position: relative;\n}\n#seller-timeline .timeline-movement.timeline-movement-top {\n  height: 60px;\n}\n#seller-timeline .timeline-movement .timeline-item {\n  padding: 20px 0;\n}\n#seller-timeline .timeline-movement .timeline-item .timeline-panel.seller-side {\n  border: 1px solid #d4d4d4;\n  border-radius: 3px;\n  /* background-color: #FFFFFF; */\n  background-color: rgb(241, 241, 241);\n  color: #666;\n  padding: 10px;\n  position: relative;\n  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);\n  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);\n}\n\n#seller-timeline .timeline-movement .timeline-item .timeline-panel.buyer-side {\n  border: 1px solid #d4d4d4;\n  border-radius: 3px;\n  background-color: #FFFFFF;